import React from 'react';

import axios from './../../axios';

import { Button , Input , message,Select } from 'antd';

import {  Link } from 'react-router-dom';



const Option = Select.Option;

function getUrl()
  {
      var json = {}
      var url = window.location.href;
      if(url.match(/\?/gi)===null)
      {
          alert('')
          return false;
      }
      var nUrl = url.split('?')[1].split('&');
      for(var i = 0;i<nUrl.length;i++)
      {
        let arr = [];
        arr = nUrl[i].split('=')
        arr[1] = arr[1].split('#')[0]
        json[arr[0]] = arr[1]
      }
     return json;
  }
class App extends React.Component{
    constructor(...args)
    {
        super(...args)
  
        this.state = {
            display:{display:'block'}
            ,
            data:this.props.date,
            params:{
            sites:getUrl().sites,
            id:'',
            parent:'', //  * 1parent
            name:'',//  *
            url:'',// 
            ico:'',// ()
            areaadmin:'',// id
            dispgroups:'',// 
            show:1,// 101
            redname:0,// 100
            sorder:'',// 
            type:''// 
            }
        }
    }
setId(e)
{
 let value = this.state.params;
  value.parent = e;
 this.setState({
     params:value
 })
}
setLevel(e)
{
    let value = this.state.params;
    value.dispgroups = e;
    this.setState({
        params:value
    })
}
setRedname(e)
{
    let value = this.state.params;
    value.redname = e;
    this.setState({
        params:value
    })
}
setShow(e)
{
    let value = this.state.params;
    value.show = e;
    this.setState({
        params:value
    })
}
setDate(e)
{
   let value = this.state.params;
   value[e.target.name] = e.target.value;
   this.setState({
       params:value
   })
}

onSumit()
{
    if(this.state.params.id==='')
    {
        message.error('ID')
        return;
    }
    if(this.state.params.name==='')
    {
        message.error('')
        return;
    }
    axios.post('/create_zone',this.state.params).then(res=>{
            message.info('')
            this.setState({
                params:{}
            })
            setTimeout(()=>{
                this.props.onSubmit()
            },500)
    }).catch(error=>{
        console.log(error)
    })
}
onColoes()
{
    window.history.back()
}
    render(){
        let Options = this.state.data.map((i,index)=>{
            return (
            <Option key={index} value={i.id}>{i.name}</Option>
            )
        }) 
        return(
            <div className='add_rom'>
               <h3 style={{textAlign:'center',lineHeight:'40px',color:'rgb(38, 121, 181)'}}></h3>
                <span>:</span>
                <Select defaultValue={''} style={{ width: 180 }} onChange={this.setId.bind(this)}>
                <Option value=''></Option>
                 {Options}
                </Select>
                <Input addonBefore="id:"	name='id' onChange={this.setDate.bind(this)}/>
                <Input addonBefore=""  name='sorder'  onChange={this.setDate.bind(this)} />
                <Input addonBefore=":" name='name' onChange={this.setDate.bind(this)}/>     
                <div>
                <span className='add_span'>:</span>
               <Select defaultValue={this.state.params.show} style={{ width: 100 }} onChange={this.setShow.bind(this)}>
                <Option value={0}></Option>
                <Option value={1}></Option>
                </Select>
                <span>:</span>
               <Select defaultValue={this.state.params.redname} style={{ width: 100 }} onChange={this.setRedname.bind(this)}>
                <Option value={0}></Option>
                <Option value={1}></Option>
                </Select>
                </div>
               <Input addonBefore=":"	name='url' onChange={this.setDate.bind(this)}/>
               <Input addonBefore=":"	name='ico' onChange={this.setDate.bind(this)}/>
               <Input addonBefore=""	name='areaadmin' onChange={this.setDate.bind(this)}/>
                          
               <div>
                <span>:</span>
               <Select defaultValue={'-1'} style={{ width: 140 }} onChange={this.setLevel.bind(this)}>
                <Option value={'-1'}></Option>
                <Option value={'199'}>VIP</Option>
                <Option value={'1999'}></Option>
                </Select>
                </div>
               <div style={{marginTop:'20px'}}>
               <Button type="primary" onClick={this.onSumit.bind(this)} style={{marginRight:'20px'}}></Button>
                <Button onClick={this.onColoes.bind(this)}></Button>
               </div>
            </div>
        )
    }
}



export default App;