import React from 'react';
import { Table, Icon ,Button , Input ,DatePicker,Alert,message} from 'antd';
import axios from './../../axios'; 
import moment from 'moment';
import 'moment/locale/zh-cn';
const { RangePicker } = DatePicker;
moment.locale('zh-cn')

function getDate(arr)
{
    if(arr===''||arr===null)
    {
        return '';
    }
  var time = new Date(arr)
 var s = '';
 s += time.getFullYear() + '-';          // 
 if (time.getMonth() < 9) {
     s += '0' + (time.getMonth() + 1) + "-";         // 
 } else {
     s += (time.getMonth() + 1) + "-";         // 
 }
 if (time.getDate() < 10) {
     s += '0' + time.getDate()+' ';                 // 
 } else {
     s += time.getDate() +' ';                 // 
 }
 if (time.getHours() < 10) {
     s += '0' + time.getHours()+ ":";                 // 
 } else {
     s += time.getHours()+ ":";                 // 
 }
 if (time.getMinutes() < 10) {
     s += '0' + time.getMinutes()+ ":";                 // 
 } else {
     s += time.getMinutes()+ ":";                 // 
 }
 if (time.getSeconds() < 10) {
     s += '0' + time.getSeconds();                 // 
 } else {
     s += time.getSeconds();                 // 
 }
 return (s);                          // 
}
function getUrl()
  {
      var json = {}
      var url = window.location.href;
      if(url.match(/\?/gi)===null)
      {
          alert('')
          return false;
      }
      var nUrl = url.split('?')[1].split('&');
      for(var i = 0;i<nUrl.length;i++)
      {
        let arr = [];
        arr = nUrl[i].split('=')
        arr[1] = arr[1].split('#')[0]
        json[arr[0]] = arr[1]
      }
     return json;
  }
var types = ['','','','','','','']
function status(arr)
{
    let tx = '';
    switch(arr){
        case 1:
        tx = ''
        break;
        case 2:
        tx = ''
        break;
        case 3:
        tx = ''
        break;
        case -1:
        tx = ''
        break;
    }
    return tx;
}
const columns = [{
  title: '',
  dataIndex: 'userId',                              
},{
  title: '',
  dataIndex: 'true_name',
},{
  title: '',
  dataIndex: 'belong',
},{
    title: '',
    dataIndex: 'phone',
},{
    title: '',
    dataIndex: 'bank_account',
},{
    title: '',
    dataIndex: 'bank_address',
},{
    title: '',
    dataIndex: 'sell_card',
},{
    title: '',
    dataIndex: 'sell',
    render:(text)=>{
        return text/100
    }
},{
    title: '',
    dataIndex: 'reward',
    render:(text)=>{
        return text/100
    }
},{
    title: '',
    dataIndex: 'cash',
    render:(text)=>{
        return text/100
    }
},{
    title: '',
    dataIndex: 'is_pay',
    render:(text)=>{
        return text/100
    }
},{
    title: '',
    dataIndex: 'to_pay',
    render:(text)=>{
        return text/100
    }
},{
    title: '',
    dataIndex: 'to_audit',
    render:(text)=>{
        return text/100
    }
},{
    title: '',
    dataIndex: 'audit',
    render:(text,record)=>{
        return (record.sell-record.reward-record.is_pay)/100
    }
}
,{
    title: '',
    dataIndex: 'wei_audit',
    render:(text,record)=>{
        return (record.sell-record.reward-record.is_pay-record.to_pay-record.to_audit)/100
    }
}];


class App extends React.Component{
   constructor(...args){
       super(...args)
   }
   state ={
    loading:false,
    pagination:{},
    sum:{
        sell_card:0,// 
        sell:0,// 
        reward:0,// 
        cash:0,// 
        is_pay:0,// 
        to_pay:0,// 
        to_audit:0 // 
    },
    order_list:[],
       params:{
        sites:getUrl().sites,
        agent_id:'',
        start_time:'',
        end_time:'',
        current:1
       },
       data:[],
   }
   componentWillMount()
   {
       this.fn(this.state.params)
   }
   fn(params){
    this.setState({
        loading:true
    })
    axios.get('/agent_sell_total',params).then(res=>{
        if(res.output.length===0)
        {
            this.setState({
                loading:false,
                data:[]
            })
        }
        var sum = this.state.sum;
        res.output.map((i,index)=>{
            sum.cash+=i.cash*1
            sum.sell_card+=i.sell_card*1
            sum.reward+=i.reward*1
            sum.sell+=i.sell*1
            sum.is_pay+=i.is_pay*1
            sum.to_pay+=i.to_pay*1
            sum.to_audit+=i.to_audit*1
           i.key = index;
           i.pay_status = status(i.pay_status)
           i.type=types[i.type*1]
           i.create_time = getDate(i.create_time)
        })
        let pagination = {};
        pagination.total = res.count*1;
        pagination.pageSize = 20;
        pagination.current = res.page*1;
        this.setState({
            sum:sum,
            data:res.output,
            loading:false,
            pagination:pagination
        })
    }).catch(error=>{
        console.log(error)
    })
   }
   searchBar()
   {
       let value = this.state.params;
       value.current = 1;
       this.fn(value)
   }
   setType1(e)
   {
     let data = this.state.params;
     data[e.target.name] = e.target.value;
     this.setState({
         params:data
     })
   }
   setType2(e,string)
   {
    let data = this.state.params;
    data.end_time = string[1] ===''?'':string[1]+ '23:59:59';
    data.start_time = string[0]===''?'':string[0]+ '00:00:00';
    this.setState({
        params:data
    })
   }
  current(e)
  {
      let value = this.state.params;
      value.current = e.current;
      this.fn(value)
  }
    render(){
        return(
            <div>
                <Alert message={<h3></h3>}
                description={<pre>
     =  -  -  <br />
     =  -  -  -  - <br />
    =  + <br />
    --
                    </pre>
                    }
                type="success"
                closable

                />
                <Alert
                style={{marginTop:'20px'}}
                message={`${this.state.params.start_time}${this.state.params.end_time} 
                 : ${this.state.sum.sell_card} ,  :${this.state.sum.sell/100} ${this.state.sum.reward/100}${this.state.sum.is_pay/100}${this.state.sum.to_pay/100}
                ${this.state.sum.to_audit/100}${(this.state.sum.sell-this.state.sum.reward-this.state.sum.is_pay)/100}:${(this.state.sum.sell-this.state.sum.reward-this.state.sum.to_audit-this.state.sum.is_pay-this.state.sum.to_pay)/100} ${this.state.sum.cash/100}`}
                type="info"
                closable
                />
                <div style={{margin:'20px 0'}}>
                <Input placeholder='ID' style={{width:'200px',margin:'0 5px'}} name='agent_id' onChange={this.setType1.bind(this)} onPressEnter={this.searchBar.bind(this)}/>
                <RangePicker format="YYYY-MM-DD  " onChange={this.setType2.bind(this)}  placeholder={['','']} name='time' showTime/>
                <Button onClick={this.searchBar.bind(this)} style={{marginLeft:'5px'}}><Icon type="search"/></Button>
                </div>
                <Table scroll={{x:true}} columns={columns} dataSource={this.state.data} bordered size='small' loading={this.state.loading} pagination={this.state.pagination} onChange = {this.current.bind(this)}/>
            </div>
        )
    }
}

export default App; 