import React from 'react';

import { Input, Select, Button, Icon} from 'antd';

const Option = Select.Option;

const options = [
    { 'label': '', 'value': 100 }, { 'label': '', 'value': 200 }, { 'label': 'VIP', 'value': 1300 }, { 'label': 'VIP', 'value': 1310 }, { 'label': '', 'value': 1320 }, { 'label': '', 'value': 1330 }, { 'label': '', 'value': 1340 }, { 'label': '', 'value': 1350 }, { 'label': '', 'value': 1360 }, { 'label': '', 'value': 1370 }, { 'label': '', 'value': 1380 }, { 'label': '', 'value': 1390 }, { 'label': '', 'value': 1391 }, { 'label': '', 'value': 1710 }, { 'label': '', 'value': 1720 }, { 'label': '', 'value': 1730 }, { 'label': '', 'value': 1740 }, { 'label': '', 'value': 1750 }, { 'label': '', 'value': 1760 }, { 'label': 'MC', 'value': 1770 }, { 'label': '', 'value': 1780 }, { 'label': '', 'value': 1900 }, { 'label': '', 'value': 1910 }, { 'label': '', 'value': 1920 },
];

function getUrl()
  {
      var json = {}
      var url = window.location.href;
      if(url.match(/\?/gi)===null)
      {
          alert('')
          return false;
      }
      var nUrl = url.split('?')[1].split('&');
      for(var i = 0;i<nUrl.length;i++)
      {
        let arr = [];
        arr = nUrl[i].split('=')
        arr[1] = arr[1].split('#')[0]
        json[arr[0]] = arr[1]
      }
     return json;
  }
class Serch_bar extends React.Component{
    constructor(...args)
    {
        super(...args)
        this.search ={
            id:"",
            roomId:"",
            nickName:"",
        }
        this.state = {
            sites:getUrl().sites,
            current:1,
            number:"", 
            interval:"",
            level:''
        }
    }

Change_id(e)
{
  this.search[e.target.name] = e.target.value
}
Change_level(value)
 {
     this.setState({
         level:value
     })   
 }
Change_number(value)
{
    this.setState({
        number: value
    })   
}
Change_interval(value)
{
    this.setState({
        interval: value
    })   
}
Change_form()
{
    this.search.current = 1;
    this.props.onSubmit(Object.assign(this.state,this.search))
}


    render()
    {
        return(
            <div className="example-input" style={{ borderBottom: "1px solid #438eb9" }}>
                <Input placeholder="ID" onChange={this.Change_id.bind(this)} style={{width:'150px'}} onPressEnter={this.Change_form.bind(this)} name='id'/>
                <Input placeholder="" onChange={this.Change_id.bind(this)} onPressEnter={this.Change_form.bind(this)} name='roomId'/>
                <Input placeholder="" onChange={this.Change_id.bind(this)}  onPressEnter={this.Change_form.bind(this)} name='nickName'/>
                 <Input placeholder="" onChange={this.Change_id.bind(this)} onPressEnter={this.Change_form.bind(this)} name='minCSB' style={{width:'140px'}}/>
                <span>:
         <Select value={this.state.level} style={{ width: 120 }} onChange={this.Change_level.bind(this)}>
         <Option value='' ></Option>
                        {
                            options.map(i => {
                                return <Option value={i.value} key={i.value}>{i.label}</Option>
                            })
                        }
                    </Select>
                </span>

                <span>:
          <Select value={this.state.number} style={{ width: 120 }} onChange={this.Change_number.bind(this)} >
                        <Option value=""></Option>
                        <Option value="2">2</Option>
                        <Option value="3">3</Option>
                        <Option value="4">4</Option>
                        <Option value="5">5</Option>
                        <Option value="6">6</Option>
                        <Option value="7">7</Option>
                        <Option value="8">8</Option>
                        <Option value="9">11</Option>
                    </Select>
                </span>

                <span>:
              <Select value={this.state.interval} style={{ width: 120 }} onChange={this.Change_interval.bind(this)}>
                       <Option value=""></Option>
                        <Option value="-1"></Option>
                        <Option value="7">7</Option>
                        <Option value="10">10</Option>
                        <Option value="15">15</Option>
                        <Option value="30">30</Option>
                        <Option value="60">60</Option>
                        <Option value="90">90</Option>
                        <Option value="180">180</Option>
             </Select>
                </span>
                <Button onClick={this.Change_form.bind(this)}><Icon type="search" /> </Button>
            </div>
        )
    }
}

export default Serch_bar;