import React from 'react';
import { Table, Icon ,Button , Input, DatePicker ,Select,Popconfirm,message} from 'antd';
import axios from './../../axios'; 


const { RangePicker } = DatePicker;
const Option = Select.Option;
function getDate(arr)
{
    if(arr===''||arr===null)
    {
        return '';
    }
  var time = new Date(arr)
 var s = '';
 s += time.getFullYear() + '-';          // 
 if (time.getMonth() < 9) {
     s += '0' + (time.getMonth() + 1) + "-";         // 
 } else {
     s += (time.getMonth() + 1) + "-";         // 
 }
 if (time.getDate() < 10) {
     s += '0' + time.getDate()+' ';                 // 
 } else {
     s += time.getDate() +' ';                 // 
 }
 if (time.getHours() < 10) {
     s += '0' + time.getHours()+ ":";                 // 
 } else {
     s += time.getHours()+ ":";                 // 
 }
 if (time.getMinutes() < 10) {
     s += '0' + time.getMinutes()+ ":";                 // 
 } else {
     s += time.getMinutes()+ ":";                 // 
 }
 if (time.getSeconds() < 10) {
     s += '0' + time.getSeconds();                 // 
 } else {
     s += time.getSeconds();                 // 
 }
 return (s);                          // 
}

