function getUrl()
  {
      var json = {}
      var url = window.location.href;
      if(url.match(/\?/gi)===null)
      {
          alert('')
          return false;
      }
      var nUrl = url.split('?')[1].split('&');
      for(var i = 0;i<nUrl.length;i++)
      {
        let arr = [];
        arr = nUrl[i].split('=')
        arr[1] = arr[1].split('#')[0]
        json[arr[0]] = arr[1]
      }
     return json;
  }
const columns = [{
  title: 'ID',
  dataIndex: 'groupid'               
},{
  title: 'id',
  dataIndex: 'client',
},{
    title: '',
    dataIndex: 'nickname',
},{
  title: '',
  dataIndex: 'type',
  render:(text)=>{
    return text===3?'':''
  }
},{
    title: '',
    dataIndex: 'obj',
    render:(text)=>{
        return json[text]
    }
},{
    title: '',
    dataIndex: 'obj_count',
},{
    title: '',
    dataIndex: 'coin',
},{
    title: '',
    dataIndex: 'score',
},{
    title: '',
    dataIndex: 'bean',
},{
    title: 'id',
    dataIndex: 'room',
},{
    title: '',
    dataIndex: 'time',
    render:(text)=>{
        return getDate(text)
    }
},{
    title: '',
    dataIndex: 'note',
},{
    title: '',
    dataIndex: 'oper'
   
}];

var color = false;
class App extends React.Component{
   constructor(...args){
       super(...args)
       this.color='table_red';
       this.search = {
        sites:getUrl().sites,
        accept_user_id:'',
        send_user_id:'',
        obj:'',
        belong:'',
        start_time:'',
        end_time:'',
        current:1
       }
   }
   state ={
    pagination:{},
    loading:false,
    save_id:'',
    data:[]
   }

   componentDidMount()
   {
       this.fn(this.search)
   }
   save_id(e)
   {
      this.setState({
        save_id:e.target.name
      })
   }
   cancel()
   {
       message.error('')
   }
   confirm()
   {
       axios.post('/restore_gift',{groupid:this.state.save_id,sites:getUrl().sites}).then(res=>{
        message.info('')
        this.fn(this.search)
       }).catch(error=>console.log(error))
   }
   fn(params){
       this.setState({
           loading:true
       }) 
    axios.get('/coin_detail',params).then(res=>{
       res.output.map((i,index,record)=>{
                i.key= index+'asd'
                i.oper = json[i.obj].match(/^|/g)!=null?'':i.oper?
                <Button style={{height:'25px',fontSize:'13px'}} ></Button>:
                <Popconfirm title={`${i.groupid}
                `} onConfirm={this.confirm.bind(this)} onCancel={this.cancel} okText="" cancelText="" >
                <Button onClick={this.save_id.bind(this)} name={i.groupid} style={{height:'25px',fontSize:'13px'}} type="primary" ></Button> 
                </Popconfirm>     
                if(index===0)
                {
                 i.color =color;
                 return;
                }
                if(i.groupid!==record[index-1].groupid)
                {
                   color=!color;
                   i.color=color;
                }
                else{
                    i.color=color;
                }
                
       })
       
       this.setState({
           data:res.output,
           loading:false,
           pagination:{
               pageSize:20,
               current:res.page*1,
               total:res.count*1
            }
       })
    })
   }
   searchBar()
   {
       this.search.current = 1;
       this.fn(this.search)
   }
   setType(e)
   {
     this.search[e.target.name] = e.target.value;
   }

   set_time(e,string)
   {
    this.search.end_time = string[1] ===''?'':string[1]+ '23:59:59';
    this.search.start_time = string[0] ===''?'':string[0]+ '00:00:00';;
   }
   current(e)
   {
       let value = this.search;
       value.current = e.current;
       this.fn(value)
   }
   setColor(i,record)
   {
      return i.color?'table_black':'table_red'
   }
   set_obj(e)
   {
      this.search.obj = e;
   }
   search_input(input, option){
    return option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
   }
    render(){
        return(
            <div className='coin_detail'>
                <div style={{margin:'20px 0'}}>
                <Input placeholder='' style={{width:'200px',margin:'0 5px'}} name='accept_user_id' onChange={this.setType.bind(this)} onPressEnter={this.searchBar.bind(this)}/>
                <Input placeholder='' style={{width:'200px',margin:'0 5px'}} name='send_user_id' onChange={this.setType.bind(this)} onPressEnter={this.searchBar.bind(this)}/>
                <span>:</span>
                <Select defaultValue='' style={{ width: 120 }} onChange={this.set_obj.bind(this)}  optionFilterProp="children" filterOption={this.search_input} showSearch>
                        <Option value=""></Option>
                       {Object.keys(json).map(i=>{
                       return <Option key={i} value={i}>{json[i]}</Option>
                       })}
               </Select>
                <Input placeholder='id' style={{width:'200px',margin:'0 5px'}} name='belong' onChange={this.setType.bind(this)} onPressEnter={this.searchBar.bind(this)}/>
                <RangePicker format="YYYY-MM-DD  " onChange={this.set_time.bind(this)}  placeholder={['','']} name='time'/>
                <Button onClick={this.searchBar.bind(this)} style={{marginLeft:'5px'}}><Icon type="search"/></Button>
                </div>
                <Table scroll={{x:true}} columns={columns}  dataSource={this.state.data} bordered size="small" loading={this.state.loading} rowClassName={this.setColor.bind(this)}  pagination={this.state.pagination} onChange={this.current.bind(this)}/>
            </div>
        )
    }
}

export default App; 